<template>
  <section class="aboutSection">
    <h1 class="heading">About me</h1>
    <p class="text-justify py-4">
      Hi there üëã. I am <span class="span">Atif</span> üòç, a web developer based
      in Faisalabad, Pakistan. I help designers, small agencies and businesses
      bring their ideas to life. Powered by <span class="span">Figma</span>,
      <span class="span">VS Code</span>
      and <span class="span">Coffee</span> ‚òï. I can turn your requirements into
      interactable visuals on <span class="span">time</span> ‚è∞ and on
      <span class="span">budget</span> üí∞.I design beautiful and powerful
      websites for modern businesses. Any business today needs a website üåê that
      wins customers‚Äô trust and helps you do your
      <span class="span">business</span> well. I make sure your website is up to
      that standard. I am <span class="span">well</span> familiar with following
      <span class="span">skills</span>.
    </p>
    <main v-if="isMounted" class="skillSet">
      <div v-for="item in skills" :key="item.id" class="skillbox">
        <img class="w-10 rounded" :src="item.src" :alt="item.name" />

        <h1 class="skillHead">{{ item.name }}</h1>
        <p class="skillText">
          {{ item.text }}
        </p>
      </div>
    </main>
    <main v-else class="w-full">
      <div class="skillSet">
        <div
          data-placeholder
          v-for="items in skeleton"
          :key="items"
          class="
            skillBox
            h-44
            md:w-56
            w-full
            overflow-hidden
            relative
            rounded-lg
            bg-mud
            animate-pulse
          "
        >
          
        </div>
      </div>
    </main>
    <main v-if="showError">
      <p class="error">
        {{ error }}
      </p>
    </main>
    <main class="py-5 flex flex-col items-center">
      <p class="sm:text-2xl text-xl font-bold text-center">
        Interested in Working with me?
      </p>
      <p class="text-center text-sm pt-2 sm:w-72">
        I am always <span class="span">happy</span> to dirty my hands on a
        <span class="span">complex</span>
        task, so do not
        <span class="span">hesitate</span> to hit me up with your requests!
      </p>
      <Buttons />
    </main>
  </section>
</template>

<script>
import Buttons from "./Buttons.vue";
import { db } from "../firebase/config";
import { collection, getDocs } from "firebase/firestore";
import Loading from "./smallLoading.vue";
import { querystring } from "@firebase/util";
export default {
  name: "About",
  components: { Buttons, Loading },
  data() {
    return {
      isMounted: false,
      skills: [],
      error: "Something bad with your internet connection",
      showError: false,
      skeleton: [1, 2, 3, 4, 5, 6, 7, 8],
    };
  },
  beforeMount() {
    this.fetchData();
    this.showError = false;
  },
  methods: {
    async fetchData() {
      const Data = collection(db, "Skills");
      try {
        const querySnapshot = await getDocs(Data);
        querySnapshot.forEach((doc) => {
          let obj = {};
          obj["name"] = doc.data().sName;
          obj["src"] = doc.data().sLink;
          obj["text"] = doc.data().sText;
          this.skills.push(obj);
        });
        if (querystring) {
          setTimeout(() => {
            this.isMounted = true;
          }, 1500);
        }
      } catch (e) {
        this.showError = true;
      }
    },
  },
};
</script>

<style>
[data-placeholder]::after {
  content: " ";
  box-shadow: 0 0 50px 9px #00dc8054;
  position: absolute;
  top: 0;
  left: -100%;
  height: 100%;
  animation: load 1s infinite;
  transform: rotate(10deg);
}
@keyframes load {
  0% {
    left: -100%;
  }
  100% {
    left: 150%;
  }
}
</style>